<template>
	<div>
		<back-header theme="文章详情"></back-header>
		<div class="banxin">
			<article v-if="hasData">
				<div class="head_wrap" >
					<h4>
					{{info[id].title}}
					</h4>
					<span class="publish_time">
					发布时间：{{info[id].time}}
					</span>
				</div>
				<div class="con_wrap" v-html="info[id].content"></div>
			</article>
		</div>
	</div>
</template>
<script>
	import backHeader from "common/backHeader";
	import axios from "axios"
	export default {
		data (){
			return {
				info:{},
				id:this.$route.query.id
			}
		},
		components:{
			backHeader
		},
		methods:{
			isEmptyObj:function(obj) {
			　　for (var key in obj){
			　　　　return false;//返回false，不为空对象
			　　}　　
			　　return true;//返回true，为空对象
			}
		},
		computed :{
			hasData:function() {
				return !this.isEmptyObj(this.info)
			}
		},
		activated:function() {
			this.id=this.$route.query.id;
		},
		mounted:function() {
			axios.get("/api/article.json").then((res)=>{
				var res=res.data;
				if(res.flag&&res.data){
					var data=res.data;
					this.info=data;					
				}
			})
		}
	}


</script>
<style scoped>
	 .banxin{
	 	padding: 0px 3%;
	     min-height: calc(100vh - 0.93rem);
	}
	 
	 .head_wrap{
	     text-align: center;
	     padding: 0.2rem 0rem 0.08rem;
	     border-bottom: 0.01rem solid #bbb;
	     margin-bottom: 0.1rem;
	}
	 article h4{
	     font-size: 0.16rem;
	     color: #555;
	     padding-bottom:0.04rem;
	}
	 .publish_time{
	     font-size: 0.12rem;
	     color: #bbb;
	     display: inline-block;
	}
	 .con_wrap{
	     text-indent: 2em;
	     line-height: 0.21rem;
	     padding: 0.1rem 0rem;
	}
	>>>.con_wrap p,>>>.con_wrap li{
	     margin: 0.1rem 0rem;
	}
	 .con_wrap{
	 	font-size: 0.12rem!important;
	 }
</style>


